/* 12px */
/* 24px */
/* 40px */
:root {
  /* Color palette */
  --cd-ocha-blue: #026cb6;
  --cd-blue--dark: #025995;
  --cd-blue--bright: #80cbff;
  --cd-highlight-red: #eb5c6d;
  --cd-white: #fff;
  --cd-grey--light: #f2f2f2;
  --cd-grey--mid: #595959;
  --cd-grey--dark: #4a4a4a;
  --cd-black: #000;
  --cd-blue-grey: #e6ecf1;
  --cd-blue-grey--light: #ebf0f4;
  --cd-max-content-width: 820px;
  --cd-max-body-width: 1400px;
  --cd-max-width: 1220px;
  --cd-container-padding: 0.75rem;
  /* 12px */
  --cd-container-padding-tablet: 1.5rem;
  /* 24px */
  --cd-container-padding-xlarge: 2.5rem;
  /* 40px */
  --cd-flow-space: 1rem;
  --cd-bumper-space: 1rem;
  --cd-footer-row-number: 5;
  /* From 01_OCHA_graphics_stylebook_visual_identity.pdf */
  --cd-red: #cd3a1f;
  --cd-red--light: #f8d8d3;
  --cd-orange: #db7b18;
  --cd-orange--light: #fae6d1;
  --cd-yellow: #d5de26;
  --cd-yellow--light: #f7f8d3;
  --cd-green: #7fb92f;
  --cd-green--light: #e8f5d6;
  /* Fonts sizes in REMs */
  --cd-font-size--tiny: 0.75rem;
  /* 12px */
  --cd-font-size--small: 0.875rem;
  /* 14px */
  --cd-font-size--base: 1rem;
  /* 16px */
  --cd-font-size--medium: 1.5rem;
  /* 24px */
  --cd-font-size--large: 2rem;
  /* 32px */
  --cd-font-size--xlarge: 2.5rem;
  /* 40px */
  --cd-font-size--xxlarge: 3rem;
  /* 48px */
}

.cd-date-month-wrapper {
  background: #272e67;
}

.cd-date-year-wrapper {
  color: #272e67;
}